#dropzone {
	background-color: #ffffbc;
	border: solid 3px #80c73c;
	min-height: 50px;
	padding: 20px;
	display: flex;
	justify-content: center;
	align-items: center;
	transition: all 0.1s;
	cursor: default;
}

#dropzone:hover {
	font-size: 1.1rem;
}

#items:empty::before {
	color: #ccc;
	content: "(File counts will be shown here.)";
}
